.--dse-button-container {
  display: flex;
  background: var(--dse-btn-primary-bg, #5ece7b);
  color: var(--dse-btn-primary-color, #ffffff);
  border: 1px solid "green";
  padding: 0.25rem;
}

.--dse-button-container:hover {
  background: var(--dse-btn-primary-bg-hover, #9ee2b0);
  border: 1px solid var(--dse-btn-primary-border-hover, #1d1f22);
  padding: 0.25rem;
}
